import React, { useState, useEffect } from 'react';
import Header from "../Components/Header";
import Bottom from "../Components/Bottom";
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import '../ResetBrowser.css';
import './panelsStyle/Swipe.css';
import Card from "../Components/Card";
import Deck from "../Components/Deck";
import connect from "@vkontakte/vk-connect";

const Swipe = ({id, go}) => {

    useEffect(() => {
        async function refreshHeaderVK() {
            document.getElementById(id).children[0].style.paddingTop = 0;
        }
        async function fetchToken(){
            let token = await connect.sendPromise("VKWebAppGetAuthToken", {
                "app_id": 7197573, "scope": "friends,events,status"});
            console.log(token);
        }
        fetchToken();
        refreshHeaderVK();
    }, []);

    return (
        <Panel id={id}>
            <Header text={"pr"}/>

            <Deck/>

            <Bottom go={go} left="personal" right="matches"/>
        </Panel>
    );
};

export default Swipe;
